import React, { useEffect } from 'react'
import "./BetsHistoryStyle.css"
const TopBets = () => {

  useEffect(()=>{
    let tabs=document.querySelectorAll(".top-tab");
    tabs.forEach((tab)=>{
     tab.addEventListener("click",()=>{
      tabs.forEach((current)=>{
        current.classList.remove("active-tab")
      });
      tab.classList.add("active-tab")
     })
    })
  },[])


  return <>


    <div className="top-tabs">
      <button className='top-tab active-tab' >monthly</button>
      <button className='top-tab'>weekly</button>
      <button className='top-tab'>daily</button>
    </div>

    <table className='table date-based-histories'>
      <thead >

      </thead>
      <tbody className='tbody'  >
        <tr >
          <td><div className="user"><img src={process.env.PUBLIC_URL + "/Photos/webPhotos/gameUser.png"} alt="" /> shubh***3</div> </td>
          <td className='bet-amount'>100.0</td>

        </tr>
        <tr>
          <td><div className="user"><img src={process.env.PUBLIC_URL + "/Photos/webPhotos/gameUser.png"} alt="" /> shubh***3</div> </td>
          <td className='bet-amount'>100.0</td>

        </tr>
        <tr>
          <td><div className="user"><img src={process.env.PUBLIC_URL + "/Photos/webPhotos/gameUser.png"} alt="" /> shubh***3</div> </td>
          <td className='bet-amount'>100.0</td>

        </tr>
        <tr>
          <td><div className="user"><img src={process.env.PUBLIC_URL + "/Photos/webPhotos/gameUser.png"} alt="" /> shubh***3</div> </td>
          <td className='bet-amount'>100.0</td>

        </tr>
        <tr>
          <td><div className="user"><img src={process.env.PUBLIC_URL + "/Photos/webPhotos/gameUser.png"} alt="" /> shubh***3</div> </td>
          <td className='bet-amount'>100.0</td>

        </tr>
      </tbody>

    </table>


  </>
}

export default TopBets
