import React, { useEffect, useState } from 'react'
import "./Gameboard.css"
import { Unity, useUnityContext } from "react-unity-webgl";

import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import $ from "jquery"

import 'owl.carousel/dist/assets/owl.theme.default.css';
import AllBets from '../Bets/AllBets';
import UserBets from '../Bets/UserBets';
import TopBets from "../Bets/TopBets"
const Gameboard = () => {
    var [firstbettingAmount, setFirstBettingAmount] = useState(10);
    var [secondBettingAmount, setSecondBettingAmount] = useState(10);
    
    const [history, setHistory] = useState(<AllBets />);


    // create active tab styling
    useEffect(()=>{
        let tabs=document.querySelectorAll(".bet-tab");
        tabs.forEach((tab)=>{
         tab.addEventListener("click",()=>{
          tabs.forEach((current)=>{
            current.classList.remove("active-tab")
          });
          tab.classList.add("active-tab")
         })
        })
      },[])

    // set the unity game options
    const { unityProvider } = useUnityContext({
        loaderUrl: process.env.PUBLIC_URL + "/Build/Build2.loader.js",
        dataUrl: process.env.PUBLIC_URL + "/Build/Build2.data",
        frameworkUrl: process.env.PUBLIC_URL + "/Build/Build2.framework.js",
        codeUrl: process.env.PUBLIC_URL + "/Build/Build2.wasm",
    });


    //Owl Carousel Settings
    const options = {
        loop: true,
        center: false,
        items:15,
        autoplay: false,
        dots: false,
        autoplayTimeout: 8500,
        smartSpeed: 450,
        nav: false,
        responsiveClass: true,

    };

    // change the color of hitory
    useEffect(()=>{
        let histories=document.querySelectorAll(".history");
        histories?.forEach((history,index)=>{
            if(index%2==0){
                history.style.color="#C86325";
            }
        })
    },[])


    return <>

        <section className="gameboard-section">
            <div className="gameboard-container">
                <div className="game-section">
                    <div className="game-history-header">
                        <ul className='histories' id='history-box'>
                            <OwlCarousel id="customer-testimonoals" className="owl-carousel owl-theme" {...options}>
                                <li className='history'>1.2x</li>
                                <li className='history'>1.08</li>
                                <li className='history'>2.0x</li>
                                <li className='history'>3.22x</li>
                                <li className='history'>1.9x</li>
                                <li className='history'>6.91x</li>
                                <li className='history'>4.3x</li>
                                <li className='history'>1.8x</li>
                                <li className='history'>2.8x</li>
                                <li className='history'>7.1x</li>
                                <li className='history'>3.2x</li>
                                <li className='history'>3.3x</li>
                                <li className='history'>2.1x</li>
                                <li className='history'>2.8x</li>
                                <li className='history'>3.4x</li>
                                <li className='history'>5.4x</li>
                                <li className='history'>5.9x</li>
                                <li className='history'>5.0x</li>
                                <li className='history'>2.8x</li>
                                <li className='history'>6.7x</li>
                            </OwlCarousel>
                        </ul>
                        <div className="history-btn">
                            <img src={process.env.PUBLIC_URL + "/Photos/webPhotos/history-btn.png"} alt="" />
                        </div>
                    </div>
                    <div className="unity-game-box">
                        <div className="canvas">
                            <Unity style={{
                                margin: 'auto'
                            }} className='unity' unityProvider={unityProvider} />
                        </div>
                    </div>

                    {/* game controllers */}

                    <div className="game-controllers-section">
                        <div className="gamecontrollers-container">
                            <div className="left-controllers">
                                <div className="header-buttons">
                                    <button className='bets-btn'>bet</button>
                                    <button className='auto-btn'>auto</button>
                                </div>
                                <div className="play-bet-controllers">
                                    <div className="apply-bet-controllers">
                                        <div className="bet-counter">
                                            <button className={firstbettingAmount < 11 ? 'decrease disabled' : 'decrease'} disabled={firstbettingAmount < 11 ? true : false} onClick={() => setFirstBettingAmount(firstbettingAmount - 5)}><i class="uil uil-minus"></i></button>
                                            <div className="count">{firstbettingAmount}</div>
                                            <button className='increse' onClick={() => setFirstBettingAmount(firstbettingAmount + 5)}>+</button>
                                        </div>
                                        <div className="bet-values">
                                            <div className="one-to-two"><div>1</div><div>2</div></div>
                                            <div className="five-to-tan"><div>5</div><div>10</div></div>
                                        </div>
                                    </div>
                                    <div className="play-bet-btn">
                                        <button>
                                            BET<br></br>
                                            {firstbettingAmount} USD
                                        </button>
                                    </div>
                                </div>
                            </div>
                            <div className="right-controllers">
                                <div className="header-buttons">
                                    <button className='bets-btn'>bet</button>
                                    <button className='auto-btn'>auto</button>

                                </div>
                                <div className="play-bet-controllers">
                                    <div className="play-bet-btn">
                                        <button>
                                            BET<br></br>
                                            {secondBettingAmount} USD
                                        </button>
                                    </div>
                                    <div className="apply-bet-controllers">
                                        <div className="bet-counter">
                                            <button className={secondBettingAmount < 11 ? 'decrease disabled' : 'decrease'} disabled={secondBettingAmount < 11 ? true : false} onClick={() => setSecondBettingAmount(secondBettingAmount - 5)}><i class="uil uil-minus"></i></button>
                                            <div className="count">{secondBettingAmount}</div>
                                            <button className='increse' onClick={() => setSecondBettingAmount(secondBettingAmount + 5)}>+</button>
                                        </div>
                                        <div className="bet-values">
                                            <div className="one-to-two"><div>1</div><div>2</div></div>
                                            <div className="five-to-tan"><div>5</div><div>10</div></div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="gamebets-section">
                    <div className="gamebets-tabs">
                        <button className='bet-tab active-tab' onClick={() => setHistory(<AllBets />)}>bets all</button>
                        <button className='bet-tab' onClick={() => setHistory(<UserBets />)}>my bets</button>
                        <button className='bet-tab'  onClick={() => setHistory(<TopBets />)}>top</button>
                    </div>
                    <div className="gamebets-details">
                        <div className="details-header">
                            <div className="total-bets">
                                All Bets<br></br>
                                <span className='bets-count'> 299</span>
                            </div>
                            <div className="previous-hand">
                                <button><i class="uil uil-clock"></i> previous hand</button>
                            </div>
                        </div>
                        <div className="details-details">
                            {/* <table className='table'>
                                <thead>
                                    <tr>
                                        <th>username</th>
                                        <th>bet amount</th>
                                        <th>bet out amount</th>
                                    </tr>
                                </thead>
                                <tbody className='tbody'>
                                    { history}
                                </tbody>
                                
                            </table>
                            */}
                            {history}
                        </div>
                    </div>
                </div>
            </div>
        </section>

    </>
}

export default Gameboard


